import axios from "axios";
import DetectRTC from "detectrtc";
const verifiedComponent = {};

(function () {
    const config = {
        videoQuality: {
            w: 1280,
            h: 720,
        },
        snapShot: {
            type: "jpeg",
            quality: 0.9,
        },
        mapCommands: {
            1: "  ",
            2: "  ",
            4: "   ",
            8: "  ",
            16: "  ",
            32: "",
        },
    };

    const UI = {
        $video: document.getElementById("video"),
        $hiddenCanvas: document.getElementById("canvas"),
        $instruction: document.getElementById("instruction"),
    };

    const Store = {
        sessionId: UI.$video.dataset["session"],
        currentCommand: 4,
    };

    const NavigatorMediaDevices =
        navigator.mediaDevices && navigator.mediaDevices.getUserMedia
            ? navigator.mediaDevices
            : navigator.mozGetUserMedia || navigator.webkitGetUserMedia
            ? {
                  getUserMedia: function (c) {
                      return new Promise(function (y, n) {
                          (navigator.mozGetUserMedia || navigator.webkitGetUserMedia).call(navigator, c, y, n);
                      });
                  },
              }
            : null;

    function Emitter() {
        this.events = {};
    }

    Emitter.prototype.on = function (type, listener) {
        this.events[type] = this.events[type] || [];
        this.events[type].push(listener);
    };

    Emitter.prototype.emit = function (type, params) {
        if (this.events[type]) {
            this.events[type].forEach(function (listener) {
                listener(params);
            });
        }
    };
    const progressProcess = new Emitter();
    //  app   
    this.progressProcess = progressProcess;
    //   
    this.runProcess = sendSnapshot;
    //  
    this.stopMediaTracks = stopMediaTracks;

    this.navigatorMediaDevices = NavigatorMediaDevices;

    this.debug = debug;
    /**
     *    
     * @returns {Promise<unknown>}
     */
    this.init = function () {
        return new Promise((resolve, reject) => {
            NavigatorMediaDevices.enumerateDevices().then(mediaDevices => {
                const firstCamera = mediaDevices.find(item => item.kind === "videoinput");
                if (firstCamera) {
                    config.constraints = {
                        video: {
                            width: { ideal: config.videoQuality.w },
                            height: { ideal: config.videoQuality.h },
                            deviceId: {
                                exact: firstCamera.deviceId,
                            },
                        },
                        audio: false,
                    };

                    navigator.mediaDevices.getUserMedia(config.constraints).then(
                        stream => {
                            Store.currentStream = stream;

                            try {
                                UI.$video.srcObject = stream;
                            } catch (error) {
                                UI.$video.src = URL.createObjectURL(stream);
                            }

                            UI.$video.play();
                            if (DetectRTC.osName === "iOS") {
                                UI.$video.setAttribute("autoplay", "");
                                UI.$video.setAttribute("playsinline", "");
                                setTimeout(calculateVideoBoxPosition, 0);
                                setTimeout(sendSnapshot, 1000);
                            } else {
                                UI.$video.onplay = () => {
                                    setTimeout(calculateVideoBoxPosition, 0);
                                    setTimeout(sendSnapshot, 1000);
                                };
                            }

                            resolve();
                        },
                        error => {
                            console.dir(error);
                            let message = JSON.stringify(error.message || error.name);
                            if (error.name === "OverconstrainedError") {
                                const v = config.constraints.video;
                                message = `      ${v.width.exact}x${v.height.exact} .`;
                            } else if (error.name === "NotAllowedError") {
                                message =
                                    "      ,        ";
                            }
                            reject(message);
                        }
                    );
                } else {
                    reject("       ");
                }
            }, reject);
        });
    };

    /**
     *    
     * @returns {string} - base64   
     */
    function takeSnapshot() {
        let context = UI.$hiddenCanvas.getContext("2d");

        let width = UI.$video.videoWidth,
            height = UI.$video.videoHeight;

        if (width && height) {
            UI.$hiddenCanvas.width = width;
            UI.$hiddenCanvas.height = height;

            context.drawImage(UI.$video, 0, 0, width, height);

            return UI.$hiddenCanvas.toDataURL(`image/${config.snapShot.type}`, config.snapShot.quality);
        }
    }

    /**
     *  
     */
    function stopMediaTracks() {
        if (Store.currentStream) {
            Store.currentStream.getTracks().forEach(track => {
                console.log("stop");
                track.stop();
            });
        }
    }

    /**
     *     
     */
    function calculateVideoBoxPosition() {
        const clientVideo = UI.$video.getBoundingClientRect();
        const videoOffset = {
            x: clientVideo.width > window.innerWidth ? Math.floor((clientVideo.width - window.innerWidth) / 2) : 0,
            y: clientVideo.height > window.innerHeight ? Math.floor((clientVideo.height - window.innerHeight) / 2) : 0,
        };
        // rotateY(180deg)   
        if (videoOffset.x && videoOffset.y) {
            UI.$video.style.transform = `translate(-${videoOffset.x}px, -${videoOffset.y}px) rotateY(180deg)`;
        } else if (videoOffset.x) {
            UI.$video.style.transform = `translateX(-${videoOffset.x}px) rotateY(180deg)`;
        } else if (videoOffset.y) {
            UI.$video.style.transform = `translateY(-${videoOffset.y}px) rotateY(180deg)`;
        } else {
            UI.$video.removeAttribute("style");
        }
    }

    const command = {
        show: text => {
            UI.$instruction.querySelector("span").innerText = text;
        },
        changeStatus: status => {
            if (status) {
                UI.$instruction.classList.add("success");
            } else {
                UI.$instruction.classList.remove("success");
            }
        },
    };

    /**
     *     
     * @param {number} countOfAttempts
     */
    function sendSnapshot(countOfAttempts) {
        //    respons server'  
        const url = location.host === "localhost:3000" ? "http://localhost:4000" : "";
        const bodyRequest = {
            photoBase64: takeSnapshot().slice(23),
            sessionId: Store.sessionId,
        };

        if (typeof countOfAttempts === "number") {
            bodyRequest.newSession = true;
        }

        axios
            .post(`${url}/sendphoto`, bodyRequest, {
                headers: {
                    "Content-Type": "application/json",
                },
            })
            .then(({ data }) => {
                if (data.result === 0) {
                    // InProcess
                    command.show(config.mapCommands[Store.currentCommand]);
                    command.changeStatus(data.stageSuccess);
                    Store.currentCommand = data.next;

                    if (data.stageSuccess) {
                        setTimeout(sendSnapshot, 1500);
                    } else {
                        setTimeout(sendSnapshot, 1000);
                    }
                } else if (data.result === 1) {
                    // Success
                    progressProcess.emit("end", "success");
                } else if (data.result === 2) {
                    // Failed
                    if (countOfAttempts || typeof countOfAttempts === "undefined") {
                        command.show("  ");
                        command.changeStatus(false);
                        progressProcess.emit("newAttempt");
                    } else {
                        progressProcess.emit("end", "error");
                    }
                }
            })
            .catch(err => {
                console.log(err);
                progressProcess.emit("end", "error");
            });
    }

    window.addEventListener(
        "resize",
        function () {
            timerDecorator(calculateVideoBoxPosition, 1000);
        },
        true
    );

    const timerDecorator = (() => {
        const timers = {};
        return (callback, ms, uniqueId) => {
            if (!uniqueId) {
                uniqueId = "Don't call this twice without a uniqueId";
            }
            if (timers[uniqueId]) {
                clearTimeout(timers[uniqueId]);
            }
            timers[uniqueId] = setTimeout(callback, ms);
        };
    })();

    function debug(content) {
        let $out = document.getElementById("debug");
        if (!$out) {
            $out = document.createElement("div");
            const $outBox = document.querySelector(".step-verified");
            $out.setAttribute("id", "debug");
            $outBox.appendChild($out);
        }
        $out.innerHTML = `
      <pre>DetectRTC: ${JSON.stringify(content, null, 2)}</pre>
      `;
    }
}.call(verifiedComponent));

export default verifiedComponent;
