import "./_scriptErrorHandler.js";
import DetectRTC from "detectrtc";
import verifiedComponent from "./_verified.js";

const UI = {
    $body: document.querySelector("body"),
    $btnNav: document.querySelector(".btn-nav"),
    $deviceStatus: document.querySelector(".device-status"),
    $messageBoxStep2: document.querySelector(".intro-text__step2"),
    $video: document.getElementById("video"),
};

const Store = {
    isDebug: true,
    //   
    activeStep: 1,
    //     
    isCamPermissionSuccess: false,
    //   
    countOfAttempts: 3,
};

const App = {
    nav: {
        /**
         *     
         * @param {number} numStep
         * @param {text} status - .     
         */
        go: (numStep, status) => {
            if (numStep >= 1 && numStep <= 3) {
                UI.$body.classList.remove("new-attempt");
                UI.$body.classList.remove("verifiedComponent");
                UI.$body.classList.remove("active-1");
                UI.$body.classList.remove("active-2");
                UI.$body.classList.remove("active-3");
                UI.$body.classList.add(`active-${numStep}`);
                if (status) {
                    UI.$body.classList.add(status);
                }
                Store.activeStep = numStep;
            }
        },
    },
    /**
     *     
     * @returns {Promise<unknown>}
     */
    checkWebCam: () => {
        return new Promise((resolve, reject) => {
            DetectRTC.load(() => {
                if (DetectRTC.hasWebcam) {
                    if (!DetectRTC.isWebRTCSupported) {
                        reject(
                            "     WebRTC.     Google Chrome, Mozila Firefox"
                        );
                        return;
                    }
                    if (!DetectRTC.isCanvasSupportsStreamCapturing) {
                        reject(
                            "      .     Google Chrome, Mozila Firefox"
                        );
                        return;
                    }
                    if (DetectRTC.isWebsiteHasWebcamPermissions) {
                        Store.isCamPermissionSuccess = true;
                        resolve();
                    } else {
                        Store.isCamPermissionSuccess = false;
                        verifiedComponent.navigatorMediaDevices
                            .getUserMedia({
                                audio: false,
                                video: true,
                            })
                            .then(
                                () => {
                                    Store.isCamPermissionSuccess = true;
                                    resolve();
                                },
                                reason => {
                                    if (reason.message && reason.message === "Permission denied") {
                                        reject(
                                            "   ,      ."
                                        );
                                    }
                                    reject(reason.message || reason.name || "Error");
                                }
                            );
                    }
                } else {
                    reject("       ");
                }
            });
        });
    },
    /**
     *   
     */
    runVerified: () => {
        UI.$btnNav.setAttribute("disabled", true);

        verifiedComponent.init().then(() => {
            UI.$body.classList.add(`verified`);
            UI.$btnNav.removeAttribute("disabled");
        }, verifiedComponent.debug);

        verifiedComponent.progressProcess.on("end", result => {
            App.nav.go(3, result);
            verifiedComponent.stopMediaTracks();
        });

        verifiedComponent.progressProcess.on("newAttempt", () => {
            UI.$btnNav.removeAttribute("disabled");
            UI.$body.classList.add("new-attempt");
            Store.countOfAttempts--;
        });
    },
    /**
     *    
     */
    runProcessAsNewAttempt: () => {
        UI.$body.classList.remove("new-attempt");
        verifiedComponent.runProcess(Store.countOfAttempts);
    },
};

UI.$btnNav.addEventListener("click", function () {
    if (Store.activeStep === 2) {
        if (Store.isCamPermissionSuccess) {
            if (Store.countOfAttempts === 3) {
                App.runVerified();
            } else {
                App.runProcessAsNewAttempt();
            }
        }
    } else {
        App.nav.go(Store.activeStep + 1);

        if (Store.activeStep === 2) {
            App.checkWebCam().then(
                () => {
                    UI.$deviceStatus.classList.remove("error");
                },
                error => {
                    UI.$deviceStatus.classList.add("error");
                    UI.$messageBoxStep2.innerText = error;
                    App.nav.go(2, "error");
                }
            );
        }
    }
});
